
document.addEventListener("DOMContentLoaded", function() {
  /* eslint-disable */
  var hamburger = getSingleByClass("hmbrgr-btn");
  var back = getById("arka");
  var header = getSingleByTag("header");
  /* eslint-disable */


  hamburger.addEventListener("click", handleMenuOpen);
  hamburger.addEventListener("keypress", function(e) {
    // eslint-disable-next-line
    if(isSpaceOrEnter(e)) handleMenuOpen(e);
  });

  function handleMenuOpen(e) {
    
    var btn = e.currentTarget;
    
    var isExpanded = function() {
      return btn.getAttribute("aria-expanded") === "true";
    } 
    
    var shouldExpand = isExpanded() ? "false" : "true";

    btn.setAttribute("aria-expanded", shouldExpand);

    updateUI();
    function updateUI() {

      back.style.display = isExpanded() ? "block" : "none";

      document.body.style.overflow = isExpanded() ? "hidden" : "initial";

      var openedOrClosed = isExpanded() ? "kapat" : "aç";

      var roleDesc = "Navigasyon menüsünü " + openedOrClosed;

      btn.setAttribute("aria-roledescription", roleDesc);
    }

    if(isExpanded()) {
      window.addEventListener("click", closeMenu)
    } else {
      window.removeEventListener("click", closeMenu)
    }

    function closeMenu(e) {
      if(e.target === back) {
        btn.setAttribute("aria-expanded", "false");
        updateUI()
      }
    }
    // if navMenu is expanded, set the focus to first link element in the list;
    // btn.getAttribute("aria-expanded") === "true" && document.querySelector("#navmenu a").focus();
  }
  // FIXED THE HEADER ON SCROLL
  var isReplaced = false;

  window.onscroll = function() {
    console.log("scrolling")
    setTimeout(() => {
      if(window.pageYOffset > header.offsetHeight) {
        if(!isReplaced) {
          createReplacer();

          isReplaced = true;
        }

        addClass("fixed", "animate-fixing");
        console.log("sınıflar eklendi")
      } else {
        if(isReplaced) {
          removeReplacer();

          isReplaced = false;
        }

        remClass("fixed", "animata-fixing");
      }
    }, 150)
  }

  function addClass(args) {
    args.forEach(function(cls) {
      header.classList.add(cls)
    })
  }

  function remClass(args) {
    args.forEach(function(cls) {
      header.classList.remove(cls)
    })
  }

  function createReplacer() {
    var replacer = document.createElement("div");

    replacer.style.height = header.offsetHeight + "px";

    replacer.className = "replacer-div";

    docuemnt.body.insertBefore(replacer, header);
  }

  function removeReplacer() {
    document.body.removeChild(getByQuery(".replacer-div"));
  }
})